.feed {
  composes: pr-2 pl-2 from global;
  height: 696px;
  width: 844px;
  display: flex;
  flex-direction: column;

  gap: 24px;
  overflow-y: scroll;
  box-sizing: border-box;
}

.section {
  width: 1250px;
  margin: 0 auto;
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.container {
  margin-top: 120px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.links {
  composes: text mr-30 from global; /* при нормальнйо ширине инпутов должно быть mr-15 */
  list-style-type: none;
  display: flex;
  flex-direction: column;
  max-width: 320px;
}

.link {
  composes: text text_type_main-medium text_color_inactive from global;
  text-decoration: none;
  height: 64px;
}

.text {
  composes: text text_type_main-default text_color_inactive mt-20 from global;
  opacity: 0.6;
}

.form {
  composes: pl-6 from global; /* при нормальнйо ширине инпутов убрать pl-6 */
}

.input {
  composes: pb-6 from global;
}

.button {
  margin-inline: auto;
  display: flex;
  justify-content: center;
}

.buttons {
  display: flex;
}
